/* eslint-disabled */
import userEvent from "@testing-library/user-event";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import MyAlert from "./MyAlert.js";
import "./Detail.scss";
import axios from "axios";

let 박스 = styled.div`
  padding: 20px;
`;

let 제목 = styled.h4`
  font-size: 25px;
  color: ${(props) => props.색상};
`;

// 구 문법
// class Detail2 extends React.Component{

//   componentDidMount(){

//   }

//   componentWillUnmount(){

//   }
// }



function Detail(props) {
 
  let history = useHistory();

  let [myAlert, myAlertB] = useState(true);

  let [inputData,inputDataB] = useState('');

  useEffect(() => {

    // axios.get(); 디테일 상품 받아오기 

    let timer = setTimeout(() => {

        myAlertB(false);
    }, 2000);


    return ()=>{ clearTimeout(timer);

    }
  },[]);



  return (
    <div className="container">
      <박스>
        <제목 className="red">Detail</제목>
      </박스>

      {inputData}
      <input onChange={ (e)=>{ inputDataB(e.target.value) }} />
      
      
      {
      myAlert === true
      ?<MyAlert></MyAlert>
      :null
      }
      <div className="row">
        <div className="col-md-6">
          <img
            src="https://codingapple1.github.io/shop/shoes1.jpg"
            width="100%"
          />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">상품명</h4>
          <p>상품설명</p>
          <p>120000원</p>

          <Info 재고={props.재고}></Info>
          <button className="btn btn-danger" onClick={ ()=>{ props.재고변경(
            ()=>{
              let new재고 = [...props.재고];
              
              new재고[0]>1
              ?new재고[0] = new재고[0]-1
              :new재고[0] = '품절';
              return new재고;
            }
          )}}>주문하기</button>
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}


function Info(props){

  return(

    <p> 재고 : {props.재고[0]}</p>
  )

}
export default Detail;
